// 需要支持的单独配置项：显示时长、是否启用背景、类型、内容
export default {
	name: 'bobo-message-cpt',
	props: {
		zIndex: {
			type: Number,
			default: 10000
		},
		duration: {
			type: Number,
			default: 2000
		},
		background: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			messageQueue: [],
			lastId: 0
		}
	},
	computed: {
		backgroundClass() {
			return msg => {
				return this.background || msg.background ? `background-${msg.type}` : ''
			}
		}
	},
	methods: {
		/**
		 * 展示普通提示信息
		 * @param {Object} content
		 */
		info(arg) {
			const message = {
				type: 'info'
			}
			if (typeof arg === 'object' && arg) {
				message.content = arg.content
				message.duration = arg.duration
				message.background = arg.background
			} else if(typeof arg === 'string') {
				message.content = arg
			}
			this.fadeIn(message)
		},
		/**
		 * 展示成功提示
		 * @param {Object} content
		 */
		success(arg) {
			const message = {
				type: 'success'
			}
			if (typeof arg === 'object' && arg) {
				message.content = arg.content
				// 显示时长会用在 settimeout的参数中，必须保证类型正确
				if (arg.duration && typeof arg.duration === 'number' && arg.duration >= 0) {
					message.duration = arg.duration
				}
				message.background = arg.background
			} else if(typeof arg === 'string') {
				message.content = arg
			}
			this.fadeIn(message)
		},
		/**
		 * 展示警告提示
		 */
		warn(arg) {
			const message = {
				type: 'warn'
			}
			if (typeof arg === 'object' && arg) {
				message.content = arg.content
				if (arg.duration && typeof arg.duration === 'number' && arg.duration >= 0) {
					message.duration = arg.duration
				}
				message.background = arg.background
			} else if(typeof arg === 'string') {
				message.content = arg
			}
			this.fadeIn(message)
		},
		/**
		 * 展示错误提示
		 * @param {Object} message
		 */
		error(arg) {
			const message = {
				type: 'error'
			}
			if (typeof arg === 'object' && arg) {
				message.content = arg.content
				if (arg.duration && typeof arg.duration === 'number' && arg.duration >= 0) {
					message.duration = arg.duration
				}
				message.background = arg.background
			} else if(typeof arg === 'string') {
				message.content = arg
			}
			this.fadeIn(message)
		},
		fadeIn(message) {
			message.id = this.generateId()
			message.animation = 'fadeIn'
			this.messageQueue.push(message)
			// 动画执行完毕后取消动画效果，防止列表刷新时重新执行动画
			setTimeout(() => {
				message.animation = ''
			}, 410)
			// 显示一段时间后隐藏
			setTimeout(() => {
				this.fadeOut(message)
			}, message.duration || this.duration)
		},
		fadeOut(message) {
			message.animation = 'fadeOut'
			setTimeout(() => {
				let idx = 0
				this.messageQueue.some((msg, index) => {
					if (msg.id === message.id) {
						idx = index
						return true
					}
				})
				this.messageQueue.splice(idx, 1)
			}, 500)
		},
		generateId() {
			return (new Date()).getTime() * 1000 + (this.lastId++) % 1000
		}
	}
}
